import React, { useEffect } from 'react';
import 'aos/dist/aos.css';
import AOS from 'aos';
import landingPageIcon from "../imagens/icons/landing-page-icon.png";
import ecommerceIcon from "../imagens/icons/ecommerce-Icon.png";
import portfolioIcon from "../imagens/icons/portifolio-icon.png";
import contentWebsitesIcon from "../imagens/icons/siteconteudo-icon.png";
import mobileIcon from "../imagens/icons/mobile-icon.png";

const TypesWebsites = () => {
    useEffect(() => {
        // Inicialize os popovers aps o carregamento do componente
        const popoverTriggerList = [].slice.call(document.querySelectorAll('[data-bs-toggle="popover"]'));
        popoverTriggerList.map(function (popoverTriggerEl) {
            return new window.bootstrap.Popover(popoverTriggerEl);
        });
    }, []);

    useEffect(() => {
        AOS.init({
            duration: 700, // Durao da animao em milissegundos
            easing: 'ease-in', // Tipo de easing
            once: false, // A animao ocorre apenas uma vez
            offset: 50, // Offset da animao
            delay: 0, // Atraso da animao
            mirror: true, // Anima ao rolar para fora tambm
        });

        // Atualizar as animaes sempre que o componente atualizar
        AOS.refresh();
    }, []);

    return (
        <>
            <h1 className="mt-5 typeswebsites-title">Tipos de Servio</h1>
            <div className="container-fluid typeswebsites-container">

                <div className="typeswebsites-list">
                    {[
                        { title: "Landing Page", icon: landingPageIcon, alt: "Landing Page", popoverTitle: "Landing Page", popoverText: "Uma landing page  uma pgina web focada em converses, criada para campanhas de marketing. Seu objetivo principal  levar o visitante a realizar uma ao especfica, como preencher um formulrio ou fazer uma compra. Ela possui contedo direto, design atraente, uma chamada para ao clara e minimiza distraes para maximizar a taxa de converso." },
                        { title: "Loja Virtual", icon: ecommerceIcon, alt: "Loja Virtual", popoverTitle: "Loja Virtual", popoverText: "Uma loja virtual  um site onde produtos ou servios so vendidos online. Ela permite que os clientes naveguem, adicionem itens ao carrinho e faam compras diretamente pela internet, com suporte para pagamento online e segurana nas transaes." },
                        { title: "Portflio", icon: portfolioIcon, alt: "Portflio", popoverTitle: "Portflio", popoverText: "Um site de portflio  uma pgina web que destaca os projetos, habilidades e experincias de um profissional ou empresa, com o objetivo de impressionar potenciais clientes ou empregadores." },
                        { title: "Conteudista", icon: contentWebsitesIcon, alt: "Conteudista", popoverTitle: "Conteudista", popoverText: "Um site conteudista  uma plataforma online focada na criao e compartilhamento de contedo informativo, educativo ou de entretenimento. Seu objetivo  atrair e engajar visitantes atravs de artigos, vdeos, podcasts ou outros formatos de mdia, geralmente com o intuito de educar, informar ou entreter o pblico-alvo." },
                        { title: "Mobile App", icon: mobileIcon, alt: "Mobile App", popoverTitle: "Mobile App", popoverText: "Um mobile app  um software projetado para ser usado em dispositivos mveis, como smartphones e tablets, oferecendo diversas funcionalidades e servios diretamente na palma da mo do usurio." },
                    ].map((service, index) => (
                        <div key={index} className="tipeswebsite-item">

                            <div data-aos="zoom-in-up">
                                <div className="card typeswebsites-card" data-bs-container="body" data-bs-toggle="popover" data-bs-trigger="hover focus" data-bs-placement="bottom" data-bs-title={service.popoverTitle} data-bs-content={service.popoverText}>
                                    <div className="card-body text-center">
                                        <h2 className="card-title">{service.title}</h2>
                                        <img src={service.icon} alt={service.alt} className="img-fluid" />
                                    </div>

                                </div>
                            </div>

                        </div>
                    ))}

                </div>
            </div>

        </>
    );
};

export default TypesWebsites;
