import React, { useState } from "react";

const Benefits = () => {
  const [isBenefitsActive, setIsBenefitsActive] = useState(true);

  const toggleBenefits = () => {
    setIsBenefitsActive((prevIsBenefitsActive) => !prevIsBenefitsActive);
  };

  const titles = {
    benefits: "Os Benefcios",
    qualities: "O que eu ofereo"
  };

  const cards = {
    benefits: [
      {
        title: "Presena Online 24/7",
        text: "Um site permite que os clientes encontrem informaes sobre sua empresa a qualquer hora do dia."
      },
      {
        title: "Credibilidade",
        text: "Um site bem projetado transmite profissionalismo e pode causar uma excelente primeira impresso."
      },
      {
        title: "Alcance Ampliado",
        text: "Um site torna sua empresa acessvel a pessoas de todo o mundo."
      },
      {
        title: "Aumento das Vendas",
        text: "Formulrios de contato e landing pages ajudam a converter visitantes em leads e clientes."
      }
    ],
    qualities: [
      {
        title: "Design Responsivo",
        text: "Seu site ser visualmente atraente em qualquer dispositivo."
      },
      {
        title: "Desempenho Rpido",
        text: "Otimizo para garantir tempos de carregamento rpidos."
      },
      {
        title: "Segurana",
        text: "Implemento prticas de segurana para proteger seus dados."
      },
      {
        title: "Personalizao",
        text: "Cada projeto  nico e adaptado s suas necessidades."
      }
    ]
  };

  const activeCards = isBenefitsActive ? cards.benefits : cards.qualities;

  return (
    <div id="simple-list-item-2" className="benefits-qualities-container">
      <div className="secondary-title mt-5 d-flex justify-content-center">
        <button type="button" onClick={toggleBenefits}>
          <h2>{isBenefitsActive ? titles.qualities : titles.benefits}</h2>
        </button>
      </div>
      <div className="primary-title">
        <h1>{isBenefitsActive ? titles.benefits : titles.qualities}</h1>

        <div className="row">
          {activeCards.map((card, index) => (
            <div className="col-md d-flex justify-content-center" key={index}>
              <div className="card">
                <div className="card-body">
                  <h5 className="card-title">{card.title}</h5>
                  <p className="card-text">{card.text}</p>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Benefits;
